import Vue from 'vue'
import Router from 'vue-router'
// import qsList from '@/components/QS-list'
// import qsData from '@/components/QS-data'
// import qsFill from '@/components/QS-fill'
// import qsEdit from '@/components/QS-edit'

Vue.use(Router)

export default new Router({
	mode:'history',
  base:'/exam/dist',
  routes: [
    { 
      path: '*', 
      component: resolve => require(['../components/public/Error.vue'], resolve)
    },
   	{
	  	path: '/',
	  	component: resolve => require(['../components/public/Home.vue'], resolve),
	  	children:[
	    	{
	        path: '/manage',
	        component: resolve => require(['../components/page/QS-list.vue'], resolve)   // vue-schart组件
	      },
		  ]
		},
    {
      path: '/login',
      component: resolve => require(['../components/public/login.vue'], resolve)   // vue-schart组件
    }
    // {
    //   path: '/fill/:num',
    //   name: 'qsFill',
    //   component: qsFill
    // },
    // {
    //   path: '/edit/:num',
    //   name: 'qsEdit',
    //   component: qsEdit
    // }
  ]
})



